'use client';

import { useState } from 'react';
import { apiClient } from '@/lib/api';
import { WebAuthnService } from '@/lib/webauthn';
import { AuthenticationOptions } from '@/types';

export function LoginForm() {
  const [username, setUsername] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!username.trim()) {
      setError('请输入用户名');
      return;
    }

    if (!WebAuthnService.isSupported()) {
      setError('您的浏览器不支持 WebAuthn');
      return;
    }

    setIsLoading(true);
    setError(null);
    setSuccess(null);

    try {
      // 1. 获取登录挑战
      const options: AuthenticationOptions = await apiClient.getLoginChallenge(username.trim());
      
      // 2. 启动 WebAuthn 认证
      const credential = await WebAuthnService.authenticate(options);
      
      // 3. 验证登录
      const result = await apiClient.verifyLogin({
        username: username.trim(),
        credential
      });

      if (result.success) {
        setSuccess('登录成功！');
        setUsername('');
        // 可以在这里重定向到用户页面
      } else {
        setError(result.message);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : '登录失败');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="max-w-md mx-auto p-6 bg-white rounded-lg shadow-md">
      <h2 className="text-2xl font-bold mb-6 text-center">Passkey 登录</h2>
      
      <form onSubmit={handleLogin} className="space-y-4">
        <div>
          <label htmlFor="username" className="block text-sm font-medium text-gray-700 mb-1">
            用户名
          </label>
          <input
            id="username"
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="请输入用户名"
            disabled={isLoading}
          />
        </div>

        <button
          type="submit"
          disabled={isLoading || !username.trim()}
          className="w-full bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {isLoading ? '登录中...' : '使用 Passkey 登录'}
        </button>
      </form>

      {error && (
        <div className="mt-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
          {error}
        </div>
      )}

      {success && (
        <div className="mt-4 p-3 bg-green-100 border border-green-400 text-green-700 rounded">
          {success}
        </div>
      )}

      <div className="mt-4 text-sm text-gray-600">
        <p className="font-medium">登录说明：</p>
        <ul className="list-disc list-inside mt-2 space-y-1">
          <li>请输入您注册时使用的用户名</li>
          <li>系统会调用您的设备进行生物识别验证</li>
          <li>无需记住密码，安全便捷</li>
        </ul>
      </div>
    </div>
  );
}